@font-face {
    font-family: NRK Etica Slab;
    font-weight: 400;
    font-style: normal;
    font-display:swap;src: url(/NRK_Etica_Slab_Book.woff2) format("woff2")
}
@font-face {
    font-family: LFT Etica;
    font-weight: 400;
    font-style: italic;
    font-display:swap;src: url(/LFT_Etica_Italic.woff2) format("woff2")
}
@font-face {
    font-family: LFT Etica Bold;
    font-weight: 700;
    font-style: italic;
    font-display:swap;src: url(/LFT_Etica_Bold.woff2) format("woff2")
}
html, body, #root{
    height:100%;
    width:100%;
    padding:0;
    margin:0;
    font-family: LFT Etica, sans-serif;
}